<template>

    <div class="order-manger d-flex flex-column col-12 p-2 mt-3 mb-5">
        <h1 class="order-manger-title text-primary font-weight-bold">訂單管理</h1>

        <div class="order-manger-selecter form-group d-flex flex-row w-100">
            <select class="order-manger-selecter-ispaid form-control col m-1 ml-0 border-0 text-primary SourceBold" name="Ispaid" v-model="Ispaid">
                <option class="text-primary SourceBold" value="全部" selected>全部</option>
                <option class="text-primary SourceBold" value="已付款">已付款</option>
                <option class="text-primary SourceBold" value="未付款">未付款</option>
            </select>
            <select class="order-manger-selecter-year form-control col m-1 border-0 text-primary SourceBold" name="yeat" v-model="YEAR">
                        <option v-for="year in years" :key="year" :selected="year == YEAR" class="text-primary SourceBold" :value="`${year}`">{{year}}</option>
            </select>
            <select class="order-manger-selecter-month form-control col m-1 mr-0 border-0 text-primary SourceBold" name="month" v-model="MONTH">
                <option v-for="month in 12" :key="month" :selected="month == MONTH" class="text-primary SourceBold" :value="month < 10 ? `0${month}` : `${month}`">{{month}}月</option>
            </select>
        </div>

        <div v-for="(item,index) in FilterData" :class="{'bg-Transparent':isYestoday(item.endDate)}" :key="index" class="order-content d-sm-flex flex-sm-row p-1 p-sm-3 mt-4 shadow">
            
            <div class="d-sm-flex flex-sm-row align-items-sm-center col-sm-4 p-0 overflow-hidden">
                <div class="order-content-image d-block w-100 img-fluid" :style="{backgroundImage:`url(${item.product.imageUrl})`}"  style="background-position: center;background-size: 100% 100%;"></div>
            </div>

            <div class="d-flex flex-column justfiy-content-between col-sm-5 mt-sm-0 mt-1">
                <div class="col-12 col-sm-9">
                    <a href="#" @click.prevent="GoToTempProduct(item.product_id)" class="order-content-product-title h5">
                    {{item.product.title}}
                    </a>
                    <div class="order-content-address pt-sm-1 SourceLight text-thrid">{{autocompleteText(item.product)}}</div>
                </div>
                <div class="d-flex flex-row flex-wrap col-12 col-md p-0 p-sm-1">
                    <div class="order-content-guest col-sm-12 p-0 order-2 text-primary SourceLight">人數 : {{item.guest}}</div>
                    <div class="order-content-date pr-1 text-primary SourceLight">日期 : </div>
                    <div class="order-content-checkInDate text-primary SourceLight">
                        <small class="order-content-small-checkInDate">入住<br></small>
                        {{item.startDate}}
                    </div>
                    <div class="order-content-dateTo pl-1 pr-1 text-primary SourceLight"><small> 至 </small></div>
                    <div class="order-content-checkOutDate text-primary SourceLight"> 
                        <small class="order-content-small-checkInDate">退房<br></small>
                        {{item.endDate}} 
                    </div>
                </div>
            </div>  

            <div class="order-content-finaltotal d-flex flex-column col-12 col-sm-3 pl-3 mt-sm-0 mt-3">
                <h5 class="text-left text-primary">$ {{item.Price}} TWD </h5>
                <a href="#" @click.prevent="GoToTempProduct(item.product_id)" class="h6">
                    在預約
                </a>
                <a href="#" v-if="item.is_paid" class="text-secondary h6">已付款</a>
                <a href="#" @click.prevent="GoToPaid(item.id)" v-else class="text-warning h6">未付款</a>
                <a href="#" @click.prevent="cancelOrder(item.id,{ product_id:item.product_id , startDate:item.startDate , endDate:item.endDate })" class="h6">
                    取消訂單
                </a>
            </div>
        </div>
    </div>

</template>
<script>
//Vuex mapGetter
import { mapGetters } from 'vuex';
import moment from 'moment';

export default {
    data() {
        return {
            Ispaid:'全部',
            YEAR: moment().format('YYYY'),
            MONTH: moment().format('MM')
        };
    },
    computed: {
        FilterData(){
            if(this.YEAR || this.MONTH){
                const isPaidFilterData =  this.orders.filter(item =>{
                    if(this.Ispaid == '已付款'){
                        return item.is_paid == true;
                    }else if(this.Ispaid == '未付款'){
                        return item.is_paid == false;
                    }else{
                        return (item.is_paid == true || item.is_paid == false);
                    }
                });

                const chooseDateFilterData = isPaidFilterData.filter(Element =>{
                    if(Element.startDate){
                        return Element.startDate.split('/')[0] === this.YEAR && Element.startDate.split('/')[1] === this.MONTH;
                    }
                });

                return chooseDateFilterData;
            }else{
                return this.orders;
            }
        },
        years(){
            let years = [];
            for(let i = 2019;i<=2038;i++){
                years.push(i)
            }
            return years;
        },
        ...mapGetters('orderModules',['orders'])
    },
    methods:{
        getOrders(){
           this.$store.dispatch('orderModules/getOrders')
        },
        async GoToPaid(id){
            await this.$store.dispatch('orderModules/payOrder',id)
            this.$store.dispatch('orderModules/getOrders')
        },
        async cancelOrder(id,data){
            await this.$store.dispatch('orderModules/cancelOrder',id);
            this.$store.dispatch('orderModules/getOrders')
            this.$store.dispatch('orderModules/DelDisableDate',data)
            console.log("get over here")
        },
        GoToTempProduct(id){
            this.$router.push(`/tempproduct/${id}`);
        },
        isYestoday(checkDate){
            const today = moment().format('YYYY-MM-DD')
            const thisDay = moment(checkDate,'YYYY-MM-DD' || 'YYYY/MM/DD').format('YYYY-MM-DD')
            return moment(today,'YYYY-MM-DD').isAfter(thisDay);
        },
        autocompleteText(product){
            return product.address ? product.address.autocompleteText : '';
        }
    },
    created() {
        this.getOrders();
    },
}
</script>
<style lang="scss">
</style>
<style lang="scss" scoped>
@import '@/assets/scss/_breakpoint.scss';
.order-manger{
    min-height: 1923px;
    &-selecter{
        &-ispaid,&-year,&-month{
            border-bottom: 1px solid #336633 !important;
            &:focus{
                border: none !important;
                border-bottom: 1px solid #336633 !important;
                box-shadow: none;
            }
        }
    }

    @include breakpoint(xs){
        &-title{
            font-size: 1.8rem !important;
        }
    }
    @include breakpoint(sm){
        &-title{
            font-size: 2rem !important;
        }
    }
}

.order-content{
    height: auto;
    &-image{
        height: 230px !important;
        @include breakpoint(sm){
            height: 75%;
        }
    }
    &-small-checkInDate,&-small-checkOutDate{
        display: none;
        @include breakpoint(xs){
            width: 100%;
            padding: 2px;
            display: inline-block;
            text-align: center;
        }
    }
    &-finaltotal{
        border-left: 1px solid #336633;
        @include breakpoint(xs){
            border-left: none;
            margin-top: 3px !important;
            padding: 8px;
            text-align: center !important;
            h5,a{
                text-align: center !important;
            }
        }
        @include breakpoint(sm){
            font-size: 1rem;
        }
    }
    @include breakpoint(xs){
        &-product-title{
            font-size: 1.5rem;
        }
        &-address{
            padding-top: .2rem;
            font-size: 80%;
        }
        &-guest{
            flex: 0 0 33.33333333%;
            text-align: center;
            padding-top: 12px !important;
        }
        &-checkInDate,&-checkOutDate{
            flex: 0 0 33.33333333%;
            padding: 3px;
            text-align: center;
            border-right: thin solid #33663369;
        }
        &-date,&-dateTo{
            display: none;
        }
    }
}
</style>



