<template>
    <div class="d-flex flex-row flex-wrap w-100 mt-3 mb-5">
        <!-- 心願攔 -->
        <div class="like-list col-12 col-md-8">
            <div v-for="(item,index) in like" :key="index" class="like-list-item d-flex flex-row flex-wrap w-100 p-1 mb-4 shadow">
                <div class="col-12 col-sm-6 col-md-6 col-lg-5 w-100 overflow-hidden">
                    <div class="like-list-image d-block w-100" :style="{backgroundImage:`url(${item.product.imageUrl})`}" style="background-position: center;background-size: 100% 100%;"></div>
                </div>

                <div class="d-flex flex-column col-lg-7 col-md-6 mt-sm-0 mt-1">
                    <i class="fas fa-trash-alt align-self-end mr-sm-n1 mr-lg-0 text-primary" @click="delLike(item['product_id'])"></i>
                    <router-link :to="`/tempproduct/${item['product_id']}`" class="like-list-title h4 text-primary">
                        {{item.product.title}}
                    </router-link>

                    <!-- 電腦的人數 -->
                    <div class="like-list-desktop-guestBtn form-group mb-0">
                        <label for="guestNum" class="text-primary">客人數</label>
                        <div class="input-group">
                            <div class="input-group-prepend">
                                <button type="button" class="btn btn-primary text-white round-circle" @click="updateItemIndex(index);updateGuest(1)">+</button>
                            </div>
                            <div id="guestNum" class="form-control text-center">{{item.guest}}</div>
                            <div class="input-group-append">
                                <button type="button" class="btn btn-primary text-white round-circle" @click="updateItemIndex(index);updateGuest(-1)">-</button>
                            </div>
                        </div>
                    </div>

                    <!-- 電腦的日曆input -->
                    <div class="like-list-desktop-dateBtn d-flex flex-row mt-1">
                        <div class="form-group w-100 pr-1 mb-0" @click="updateItemIndex(index);getDisableDate(item.product.id)">
                            <label for="exampleInputEmail1" class="text-primary">入住/退房</label>
                            <VueHotelDatepicker
                            class="VueHotelDatepicker w-100"
                            :isRound="true"
                            :startDate="item.startDate"
                            :endDate="item.endDate"
                            :maxDate="maxDate"
                            :placeholder="today"
                            :disabledDates="disableDate"
                            @confirm="updateItemDate"
                            :mobile="'mobile'" />
                        </div>
                    </div>

                    <!-- 手機的人數 -->
                    <div class="like-list-mobile-guestBtn d-flex p-1 mb-0 form-group">
                        <label for="guest" class="d-flex flex-row align-items-center w-50 text-primary h5">人數</label>
                        <div class="d-flex flex-row align-items-center justify-content-between w-50 input-group">
                            <button type="button" class="btn btn-primary text-white circle m-0" @click="updateItemIndex(index);updateGuest(1)">
                                <plusSign class="plus"/>
                            </button>
                            <span>{{guest}}</span>
                            <button type="button" class="btn btn-primary text-white circle m-0" @click="updateItemIndex(index);updateGuest(-1)">
                                <subtractSign class="subtract"/>
                            </button>
                        </div>
                    </div>

                    <!-- 手機的日曆input -->
                    <div class="like-list-mobile-dateBtn d-flex flex-row mt-1">
                        <div class="form-group w-100 mb-0">
                            <label for="checkInOut" class="text-primary w-100">入住/退房</label>
                            <button @click="updateItemIndex(index);openDatePage();getDisableDate(item.product.id)" class="w-100 btn btn-white border round-circle">{{item.startDate}} ~ {{item.endDate}}</button>
                        </div>
                    </div>
                    
                    <h5 id="" class="like-list-finalTotal text-right text-primary font-weight-bold">$ {{item.Price}} TWD </h5>
                    
                    <!-- 手機的 日曆視窗 -->
                    <div class="like-list-datePage w-100 position-fixed bg-white d-none sildeOut">
                        <!-- 關閉按鈕 -->
                        <i @click="openDatePage" class="fas fa-times text-primary" aria-label="close button"></i>
                        <!-- 日曆元件 -->
                        <MobileHotelDatePicker 
                            class="like-list-datePage-DatePicker"
                            :isRound="true"
                            :startDate="item.startDate"
                            :endDate="item.endDate"
                            :maxDate="maxDate"
                            :placeholder="today"
                            :disabledDates="disableDate"
                            @confirm="updateItemDate"
                            :ParentMethod="openDatePage"
                            :mobile="'mobile'" />
                    </div>

                </div>
            </div>
        </div>
        <!-- 電腦表單 -->
        <form @submit.prevent="form.submit();CheckOut()" class="checkOut-form d-flex flex-column col-md-4 w-100 p-2 bg-success shadow-sm">
            <h3 class="text-primary font-weight-bold">個人資料</h3>
            <div class="mt-5 mt-md-4 mt-lg-5">
                <vuex-text label="暱稱"
                        name="user.data.name"
                        class="valid-input text-primary"
                        placeholder="Golden"
                        v-model="form.user.data.name"
                        :errors="form.errors.get('user.data.name')"
                        @event="form.listen($event)"
                        required
                ></vuex-text>

                <vuex-text label="連絡電話"
                        name="user.data.tel"
                        type="tel"
                        mask="(##) ####-####"
                        placeholder="(09) 0000-0008"
                        class="valid-input text-primary"
                        v-model="form.user.data.tel"
                        :errors="form.errors.get('user.data.tel')"
                        @event="form.listen($event)"
                        required
                ></vuex-text>

                <vuex-text label="Email"
                        name="user.data.email"
                        type="email"
                        placeholder="Example@gmail.com"
                        class="valid-input text-primary"
                        v-model="form.user.data.email"
                        :errors="form.errors.get('user.data.email')"
                        @event="form.listen($event)"
                        required
                ></vuex-text>

                <vuex-text label="Message"
                        name="message"
                        type="textarea"
                        class="valid-input text-primary"
                        v-model="form.message"
                        :errors="form.errors.get('message')"
                        @event="form.listen($event)"
                ></vuex-text>
            </div>
            <div class="text-primary font-weight-normal text-right">訂單摘要</div>
            <div class="checkOut-form-totalPrice pt-2 mt-1 form-group">
                <h4 class="float-left p-0 text-right text-primary">總價</h4>
                <h4 class="float-right p-0 text-right text-primary">{{ totalPrice }}</h4>
            </div>
            
            <button type="submit" class="checkOut-form-submit-btn align-self-end mt-auto btn btn-primary text-white font-weight-normal">確認</button>
        </form>

        <!-- 手機的 底部預定按鈕引導列 -->
        <div class="like-list-bottom-nav d-flex flex-row justify-content-between align-items-center vw-100 position-fixed p-1 bg-white">
            <!-- 價位 -->
            <h5 class="col-7 text-primary"><small>總價</small> $ {{ totalPrice }} TWD</h5>
            <!-- booking按鈕 -->
            <button @click="openbookIngPages" class="col btn btn-primary">預定</button>
        </div>

        <!-- 手機的 預定視窗 -->
        <form @submit.prevent="form.submit();CheckOut()" class="like-list-bookIngForm vw-100 position-fixed bg-white d-none sildeOut ml-n1 p-3">
            <!-- 關閉按鈕 -->
            <i @click="openbookIngPages" class="mb-1 fas fa-times text-primary" aria-label="close button"></i>
            <h3 class="mb-1 text-primary font-weight-bold">個人資料</h3>
            <div class="mt-5 mt-md-4 mt-lg-5">
                <!-- 暱稱 -->
                <vuex-text label="暱稱"
                        name="user.data.name"
                        class="valid-input text-primary"
                        placeholder="Golden"
                        v-model="form.user.data.name"
                        :errors="form.errors.get('user.data.name')"
                        @event="form.listen($event)"
                        required
                ></vuex-text>
                <!-- 連絡電話 -->
                <vuex-text label="連絡電話"
                        name="user.data.tel"
                        type="tel"
                        mask="(##) ####-####"
                        placeholder="(09) 0000-0008"
                        class="valid-input text-primary"
                        v-model="form.user.data.tel"
                        :errors="form.errors.get('user.data.tel')"
                        @event="form.listen($event)"
                        required
                ></vuex-text>
                <!-- Email -->
                <vuex-text label="Email"
                        name="user.data.email"
                        type="email"
                        placeholder="Example@gmail.com"
                        class="valid-input text-primary"
                        v-model="form.user.data.email"
                        :errors="form.errors.get('user.data.email')"
                        @event="form.listen($event)"
                        required
                ></vuex-text>
                <!-- 其他要求備註 -->
                <vuex-text label="Message"
                        name="message"
                        type="textarea"
                        class="valid-input text-primary"
                        v-model="form.message"
                        :errors="form.errors.get('message')"
                        @event="form.listen($event)"
                ></vuex-text>
            </div>
            <div class="text-primary font-weight-normal text-right">訂單摘要</div>
            <div class="like-list-bookIngForm-totalPrice p-1 pt-2 mt-1 form-group">
                <h4 class="float-left p-0 text-left text-primary">總價</h4>
                <h4 class="float-right p-0 text-right text-primary">{{ totalPrice }}</h4>
            </div>
            
            <button type="submit" class="like-list-bookIngForm-submit-btn w-100 mt-5 btn btn-primary text-white font-weight-normal">確認</button>
        </form>
    </div>
</template>
<script>
//JQ
import $ from'jquery';
import { mapGetters } from 'vuex';
//Datepicker
import VueHotelDatepicker from '@northwalker/vue-hotel-datepicker/src/components/VueHotelDatepicker.vue'
import MobileHotelDatePicker from '@/components/MobileHotelDatePicker.vue'
//symobl
import plusSign from '@/components/icon/plusSign'
import subtractSign from '@/components/icon/subtractSign'
//vuex-form 表單驗證
import { Form } from "vuex-forms";
//表單最小長度限制與不得為空
import { required, email , minLength } from "vuex-forms/src/validators";

export default {
    data() {
        return {
            index:0,
            form: new Form(this, {
                message:'',
                user:{
                    data:{
                        name: '',
                        tel:'',
                        email:''
                    }
                }
            }, {
                validations: {
                    message: {},
                    user:{
                        data:{
                            name: { required },
                            tel:{required , minLength:minLength(10)},
                            email:{required , email}
                        }
                    }
                }
            })
        };
    },
    components:{
        VueHotelDatepicker,
        MobileHotelDatePicker,
        plusSign,
        subtractSign
    },
    computed:{
        ...mapGetters('orderModules',['disableDate','disabledaysNum']),
        ...mapGetters('cartModules',['like','totalPrice','planformfee']),
        ...mapGetters('searchEngine',['today','maxDate','guest']),
    },
    methods: {
        //刪除心願單
        delLike(id){
            this.$store.dispatch('cartModules/DelLike',id)
        },
        //更新心願單一項目數值
        updateItemDate(data){
            this.$store.dispatch('orderModules/ThisDisableDayisBetween',{ start:data.start,end:data.end });
            this.$store.commit('cartModules/UPDATEITEMDATE',{ data , index:this.index})
            this.$store.dispatch('cartModules/updateLikeItemPrice',{ index:this.index , DisableDate:this.disabledaysNum })
            localStorage.setItem('LIKE', JSON.stringify(this.like)); //儲存到localStorage
        },
        updateGuest(num){
            this.$store.commit('cartModules/UPDATEITEMGUEST',{index:this.index,num});
            this.$store.dispatch('cartModules/updateLikeItemPrice',{ index:this.index , DisableDate:this.disabledaysNum })
            localStorage.setItem('LIKE', JSON.stringify(this.like)); //儲存到localStorage
        },
        //選取項目index位置，已更新單一項目數值
        updateItemIndex(index){
            this.index = index;
        },
        //送出訂單表單
        async CheckOut(){
            //like 空蕩蕩時跳警告
            if(this.like.length > 0){
                await this.like.forEach(item=>{
                    const order = Object.assign({},item,{is_paid:false},{
                        form:{
                            message:this.form['_data'].message,
                            user:{
                                name: this.form['_data'].user.data.name,
                                email: this.form['_data'].user.data.email,
                                tel: this.form['_data'].user.data.tel,
                                address: "Tainan"
                            }
                        }
                    });
                    this.$store.dispatch('orderModules/orderCheckOut',order);
                    //set the choose one dorm disable date 
                    this.$store.dispatch('orderModules/setDisableDate',{ title:item.product.title , category:item.product.category , id: item.product.id , start: item.startDate, end:item.endDate})
                });
            }else{
                this.$bus.$emit('messsage:push','心願單為空','warning')
            }
        },
        //取得Disable date
        getDisableDate(id){
            this.$store.dispatch('orderModules/getDisableDate',id);
        },
        //打開版手機 "日曆" 與 "預定" 的滑入式頁面
        openbookIngPages(){
            $('.like-list-bookIngForm')
            .toggleClass('sildeOut sildeIn')
            .delay(800)
            .toggleClass('d-none')
            .css('height','100vh');
        },
        openDatePage(){
            $('.like-list-datePage')
            .toggleClass('sildeOut sildeIn')
            .delay(800)
            .toggleClass('d-none')
            .css('height','100vh');
        },
    },
    created() {
        //取得心願單
        this.$store.dispatch('cartModules/getLike')
    },
}
</script>

<style lang="scss" scoped>
@import '@/assets/scss/_breakpoint.scss';
li{
    display: inline-block;
}
ul{
    padding-inline-start:0;
}

.like-list{
    max-height: 607px;
    overflow-y: scroll;
    @include breakpoint(sm){
        min-height: 100vh;
        overflow:inherit;
    }
    &::-webkit-scrollbar{
        width: 5px;
        background-color: #86b386;
        border-radius: 50px;
    }
    &::-webkit-scrollbar-thumb{
        height: 10px;
        background-color: #4b7a4b;
        border-radius: 50px;
    }
    @include breakpoint(sm){
        &::-webkit-scrollbar{
            width: 0;
        }
    }
    &-item{
        height: auto;
        &:last-child{
            margin-bottom: 0 !important;
            @include breakpoint(sm){
                margin-bottom: 60px !important;
            }
        }
        .fa-trash-alt{
            position: absolute;
            font-size: 1rem;
            right: 10px;
            top: 0;
            @include breakpoint(sm){
                font-size: 1.5rem;
            }
        }
    }
    &-image{
        height: 196px;
        @include breakpoint(sm){
            height: 240px;
        }
    }
    &-desktop-dateBtn{
        display: block !important;
        @include breakpoint(sm){
            display: none !important;
        }
    }
    &-desktop-guestBtn{
        display: block !important;
        @include breakpoint(sm){
            display: none !important;
        }
    }
    &-mobile-dateBtn{
        display: none !important;
        @include breakpoint(sm){
            display: flex !important;
        }
    }
    &-mobile-guestBtn{
        display: none !important;
        @include breakpoint(sm){
            display: flex !important;
        }
    }
    /deep/ &-datePage{
        @include breakpoint(sm){
            z-index:12;
            top:800px;
            left:0;
            transition: .5s cubic-bezier(0.075, 0.82, 0.165, 1);
            .fa-times{
                z-index: 12;
                font-size: 2rem;
                padding: 1rem;
                right: 0;
            }
            /deep/ &-DatePicker{
                width: 100vw;
                height: 420px;
            }
        } 
    }
    &-finalTotal{
        margin-top: 10px;
    }
}

.like-list-bottom-nav{
    display: none !important;
    @include breakpoint(sm){
        display: flex !important;
        left:0;
        bottom:0px;
        z-index:10;
        box-shadow: 0 0 10px #00000024;
        .fa-star{
            font-size: 10px;
        }
    }
}
.like-list-bookIngForm{
    @include breakpoint(sm){
        z-index: 11;
        top: 800px;
        right: 0;
        overflow-y: scroll;
        &-totalPrice{
            margin-top: 36px !important;
            border-top: 1px solid #336633 !important;
        }
        .fa-times{
            z-index: 11;
            font-size: 2rem;
            right: 0;
        }
        /deep/ .form-errors{
            color: #dc3545;
        }
        /deep/ .valid-input > input{
            border-radius: 50px;
        }
    }
}

.checkOut-form{
    height: auto;
    @include breakpoint(sm){
        display: none !important;
    }
    &-totalPrice{
        border-top: 1px solid #336633;
    }
    /deep/ .form-errors{
        color: #dc3545;
    }
    /deep/ .valid-input > input{
        border-radius: 50px;
    }
    &-submit-btn{
        width: 50%;
    }
}

</style>



