<template>
    <div>
        <div class="container ">
            <nav class="navbar navbar-expand-lg navbar-light col-12 mt-xl-5 mt-5 d-flex align-items-center p-0">
                <img src="@/assets/logo-all-dark.svg" alt="Sweetast" class="col-md-3 col-5 p-0">
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse" id="navbarNav">
                    <ul class="navbar-nav d-flex flex-column flex-lg-row align-items-center ml-auto">
                        <li class="nav-item active mr-lg-6 mb-3">
                            <router-link
                            data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation"
                            to="/index" class="h6 text-primary font-weight-bold">首頁</router-link>
                        </li>
                        <li class="nav-item active mr-lg-6 mb-3">
                            <router-link
                            data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation"
                            to="/dormitory" class="h6 text-primary font-weight-bold">房型介紹</router-link>
                        </li>
                        <li class="nav-item mr-lg-6 mb-3">
                            <router-link
                            data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation"
                            to="/likelist" class="h6 text-primary font-weight-bold">心願單</router-link>
                        </li>
                        <li class="nav-item mr-lg-6 mb-3">
                            <router-link
                            data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation"
                            to="/ordermanger" class="h6 text-primary font-weight-bold">訂單管理</router-link>
                        </li>
                        <li class="nav-item mr-lg-6 mb-3">
                            <router-link
                            data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation"
                            to="/admin/Dashboard/products" class="h6 text-primary font-weight-bold">後臺管理</router-link>
                        </li>
                        <li class="nav-item mb-3">
                            <router-link
                            data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation"
                            v-if="msg = '以登出'" to="/login" class="navbar-brand text-primary font-weight-bold mr-0">登入</router-link>
                        </li>
                    </ul>
                </div>
            </nav>

        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            login:''
        };
    },
    mounted() {
        this.$bus.$on('loginRes:push',payload=>{
            this.login = payload;
        });
    },
}
</script>

<style>
nav{
    list-style: none;
}
</style>



