<template>
    <div>
        <AlertMsg/>
        <div class="container-fluid">
            <div id="Dashboard" class="d-flex flex-row flex-wrap align-items-lg-stretch m-n1">
                <Sidebar class="Sidebar col-7 col-sm-4 col-md-3 col-lg-2 bg-secondary shadow"></Sidebar>
                <main role="main" class="col-12 col-sm-12 col-lg-10 p-3">
                    <div class="main-top-nav w-100 p-1 bg-white shadow rounded mb-3">
                        <a class="nav-link text-primary text-right" href="#" @click="Sidebarshow" style="right:5px;"><i class="fas fa-bars"></i></a>
                    </div>
                    <router-view></router-view>
                </main>
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
import Sidebar from '@/components/Dashboard/sidebar';
import Navbar from '@/components/Dashboard/navbar';
import AlertMsg from '../AlertMessage';

export default {
  components:{
      Sidebar,
      Navbar,
      AlertMsg
  },
  methods:{
    Sidebarshow(){
        $('.Sidebar')
        .toggleClass('sildeRightOut')
    }
  }
};
</script>

<style lang='scss'scoped>
@import '@/assets/scss/_breakpoint.scss';
#Dashboard{
    height: 100vh;
    .Sidebar{
        position: relative;
        @include breakpoint(xs){
            left: -250px;
        }
        @include breakpoint(sm){
            left: -600px;
        }
        @include breakpoint(md){
            position: fixed;
            top: 0px;
            left: -600px;
            z-index: 10;
            height: 100vh;
            transition: .5s cubic-bezier(0.075, 0.82, 0.165, 1);
            &::after{
                width: 100vw;
                z-index: 8;
                background-color: #000;
                opacity: 0.5;
            }
            .nav-link{
                i{
                    font-size: 1rem;
                }
                span{
                    font-size: 1.2rem;
                }
            }
        }
    }
    .main{
        background-color: #F6F6F6;
        &-top-nav{
            display: none !important;
        }
        @include breakpoint(sm){
            &-top-nav{
                display: block !important;
            }
        }
    }
}
</style>



