<template>
    <div class="orderlist p-3 bg-white chart-shadow rounded">
        <table class="table w-100">
            <thead>
                <tr>
                    <th class="text-left" style="width:30%" @click="isreverseDate = !isreverseDate">
                        選購房型
                        <template v-if="isreverseDate === false">
                            <span>
                            <i class=" fas fa-angle-up text-primary"></i>
                            </span>
                        </template>
                        <template v-else-if="isreverseDate === true">
                            <span>
                            <i class="fas fa-angle-down text-primary"></i>
                            </span>
                        </template>
                    </th>
                    <th class="text-left" style="width:25%">訂單資料</th>
                    <th class="text-center" style="width:15%" @click="isreversePrice = !isreversePrice">
                        應付金額
                        <template v-if="isreversePrice === false">
                            <span>
                            <i class=" fas fa-angle-up text-primary"></i>
                            </span>
                        </template>
                        <template v-else-if="isreversePrice === true">
                            <span>
                            <i class="fas fa-angle-down text-primary"></i>
                            </span>
                        </template>
                    </th>
                    <th class="text-center" style="width:15%">是否付款</th>
                    <th class="text-center" style="width:15%">刪除訂單</th>
                </tr>
            </thead>
            <tbody class="SourceLight">
                <tr v-for="(item) in sortData" :key="item.id" :class="{'bg-Transparent':isYestoday(item.endDate)}">
                    <td>
                        <div class="border-bottom pb-1 " :key="item.id">
                            <span class="font-weight-bold text-primary">{{item.product.title}}</span><br>
                            <span class="text-forth">入住 {{item.startDate}} / 退房 {{item.endDate}}</span>
                        </div>
                    </td>
                    <td class="">
                        <div class="d-flex flex-row flex-md-column">
                            <label class="font-weight-bold text-primary mb-0">訂房人:</label>
                            <p class="text-forth mb-0">{{item.form.user.name}}</p>
                        </div>
                        <div class="d-flex flex-row flex-md-column">
                            <label class="font-weight-bold text-primary mb-0">連絡電話:</label>
                            <p class="text-forth mb-0">{{item.form.user.tel}}</p>
                        </div>
                        <div class="d-flex flex-row flex-md-column">
                            <label class="font-weight-bold text-primary mb-0">連絡email:</label>
                            <p class="text-forth mb-0">{{item.form.user.email}}</p>
                        </div>
                    </td>
                    <td class="text-center">
                        {{item.Price}}
                    </td>
                    <td class="text-center">
                        <span class="text-success" v-if="item.is_paid">已付款</span>
                        <span class="text-secondary" v-else>尚未付款</span>
                    </td>
                    <td class="text-center">
                        <a @click.prevent="cancelOrder(item.id,{ product_id:item.product_id , startDate:item.startDate , endDate:item.endDate })" href="#" class="text-forth" v-if="isYestoday(item.endDate)">刪除</a>
                    </td>
                </tr>
            </tbody>
        </table>



        <!-- pagination -->
        <nav aria-label="Page navigation example">
            <ul class="pagination mb-0">
                <li class="page-item">
                <a class="page-link" href="#" aria-label="Previous" @click.prevent="getOrderList(current_page - 1)">
                    <span aria-hidden="true">&laquo;</span>
                </a>
                </li>
                <li class="page-item" v-for="page in pagination" :key="page"><a class="page-link" href="#" @click.prevent="getOrderList(page)">{{page}}</a></li>
                <li class="page-item">
                <a class="page-link" href="#" aria-label="Next" @click.prevent="getOrderList(current_page + 1)">
                    <span aria-hidden="true">&raquo;</span>
                </a>
                </li>
            </ul>
        </nav>



    </div>
</template>

<script>
//Vues mapGetters
import { mapGetters } from 'vuex';
import moment from 'moment';
export default {
    data(){
        return{
            orders:[],
            pagination:1,
            isLoading:false,
            current_page:1,
            status:{
                fileUploading:false,
            },
            isreversePrice:false,
            isreverseDate:false,
            proxyTableTarget:'https://limitless-meadow-70828.herokuapp.com'
        };
    },
    computed:{
        sortData(){
            let DateFilterData = [];
            if(this.isreverseDate == true){
                DateFilterData = this.orders.sort((a,b)=>{
                    let dateA = moment(a.startDate,'YYYY/MM/DD').format('x');
                    let dateB = moment(b.startDate,'YYYY/MM/DD').format('x');
                    return (Number(dateA) === Number(dateB) ? 0 : Number(dateA) > Number(dateB) ? 1 : -1);
                });
            }else{
                DateFilterData = this.orders.sort((a,b)=>{
                    let dateA = moment(a.startDate,'YYYY/MM/DD').format('x');
                    let dateB = moment(b.startDate,'YYYY/MM/DD').format('x');
                    return (Number(dateA) === Number(dateB) ? 0 : Number(dateA) < Number(dateB) ? 1 : -1);
                });
            }
            return DateFilterData;
            // let PriceFilterData = [];
            // if(this.isreversePrice == true){
            //     PriceFilterData = DateFilterData.sort((a,b)=>{
            //         return (a.Price === b.Price ? 0 : a.Price > b.Price ? 1 : -1);
            //     });
            // }else{
            //     PriceFilterData = DateFilterData.sort((a,b)=>{
            //         return (a.Price === b.Price ? 0 : a.Price < b.Price ? 1 : -1);
            //     });
            // }
            //console.log('PriceFilterData',PriceFilterData[0].startDate,PriceFilterData[1].startDate)
        }
    },
    methods:{
        async getOrderList(page = 1){
            const url = `${this.proxyTableTarget}/orders?_page=${page}&_limit=4`;
            await this.$http.get(url).then((res) =>{
                if(res.data.length > 0){
                    console.log(res,"getOrders : getOrderList 請求成功")
                    this.orders = res.data;

                    this.pageNum(res.headers['x-total-count'])
                    this.current_page = page;
                }
            },(err) =>{
                console.log(err,"getOrders : GET 請求失敗");
            });
        },
        async cancelOrder(id,data){
            await this.$store.dispatch('orderModules/cancelOrder',id);
            this.$store.dispatch('orderModules/getOrders')
            this.$store.dispatch('orderModules/DelDisableDate',data)
            console.log("get over here")
        },
        pageNum(total_count){
            this.pagination = Math.ceil(total_count / 4)
        },
        isYestoday(checkDate){
            const today = moment().format('YYYY-MM-DD')
            const thisDay = moment(checkDate,'YYYY-MM-DD' || 'YYYY/MM/DD').format('YYYY-MM-DD')
            return moment(today,'YYYY-MM-DD').isAfter(thisDay);
        }
    },
    created(){
        this.getOrderList();
    },
}
</script>
<style lang="scss" scoped>
@import '@/assets/scss/_breakpoint.scss';
@import '@/assets/scss/_table.scss';
.orderlist{
    @include breakpoint(sm){
        @include mobileTable(calc(100vh - 80px),calc(100vh - 160px),calc(100% / 5));
    }
}
</style>




