import axios from 'axios'
//Moment Js 處理時間
import moment from 'moment';

export default{
    strict:true,
    namespaced:true,
    state:{
        orders:[],
        disableDate:[],
        disabledaysNum:0,
        DaysBetweenDates:[],
        proxyTableTarget:'https://limitless-meadow-70828.herokuapp.com'
    },
    actions:{
        // 送出訂單
        async orderCheckOut(context,payload){
            const url = `${context.state.proxyTableTarget}/orders`;
            const order = payload
            await axios.post(url,order).then((res) =>{
                console.log('POST 成功',res)
            },(err)=>{
                console.log('POST 失敗',err)
            });
        },
        //取得所有訂單
        async getOrders(context){
            const url = `${context.state.proxyTableTarget}/orders`;
            await axios.get(url).then((res) =>{
                if(res.data.length > 0){
                    console.log(res,"getOrders : GET 請求成功")
                    context.commit('ORDERS',res.data);
                }
            },(err) =>{
                console.log(err,"getOrders : GET 請求失敗");
            });
        },
        async cancelOrder(context,id){
            const url = `${context.state.proxyTableTarget}/orders/${id}`;
            context.dispatch('updateLoading',true,{root:true})
            await axios.delete(url).then((res) =>{
                context.dispatch('updateLoading',false,{root:true})
                console.log(res,"DELETE 請求成功")
            },(err) =>{
                console.log(err,"DELETE 請求失敗");
            });
        },
        async payOrder(context,id){
            const url = `${context.state.proxyTableTarget}/orders/${id}`;
            context.dispatch('updateLoading',true,{root:true})
            await axios.patch(url,{is_paid:true}).then((res) =>{
                context.dispatch('updateLoading',false,{root:true}) 
                console.log(res,"PATCH 請求成功")
            },(err) =>{
                console.log(err,"PATCH 請求失敗");
            });
        },
        //送出訂單前，先做設定disable date先
        setDisableDate(context,{ title , category , id , start , end }){
            let date = [];
            let newObj = []
            //先取得資料確認有沒有這筆紀錄，有就合併舊的，沒有舊建立新的完整物件
            axios.get(`${context.state.proxyTableTarget}/disableDate?id=${id}`).then((res) => {
                //console.log(res,'GET 請求成功');
                if(res.data.length > 0){

                    date = res.data[0].date;
                    //列出入住到退房期間所有天數
                    context.dispatch('enumerateDaysBetweenDates',{startDate:start,endDate:end})
                    //合併舊的
                    let data = { "date":[...date,...context.state.DaysBetweenDates] }
                    //patch 到JSON SEVER做局部修改
                    axios.patch(`${context.state.proxyTableTarget}/disableDate/${id}`,data).then((res) => {
                        console.log(res,'PATCH 請求成功')
                    },(err)=>{
                        console.log(err,'PATCH 請求失敗');
                    });
                    //console.log([...date,...context.state.DaysBetweenDates])

                }else{
                    //列出入住到退房期間所有天數
                    context.dispatch('enumerateDaysBetweenDates',{startDate:start,endDate:end})
                    //新的完整物件
                    newObj = {
                        title:title,
                        category:category,
                        id:id,
                        date:[...context.state.DaysBetweenDates]
                    }
                    //POST到JSON SEVER做新增動作
                    axios.post(`${context.state.proxyTableTarget}/disableDate`,newObj).then((res) =>{
                        console.log(res,'POST 請求成功');
                    },(err)=>{
                        console.log(err,'POST 請求失敗');
                    })

                    
                }
            },(err)=>{
                console.log(err,'GET 請求失敗');
            });

        },
        //取得disable date
        getDisableDate(context,payload){
            axios.get(`${context.state.proxyTableTarget}/disableDate?id=${payload}`).then((res) => {
                if(res.data.length > 0){
                    console.log(res,'GET 請求成功')
                    context.commit('DISABLEDATE',res.data[0].date);
                }else{
                    console.log(res,'GET 請求成功 這間還未有人預定過')
                    context.commit('DISABLEDATE', new Array() );
                }
            },(err)=>{
                context.commit('DISABLEDATE', new Array() );
                console.log(err,'GET 請求失敗');
            });
        },
        DelDisableDate(context,data){
            axios.get(`${context.state.proxyTableTarget}/disableDate?id=${data.product_id}`).then((res) => {
                if(res.data.length > 0){
                    console.log(res,'DelDisableDate : GET 請求成功')
                    //計算這筆訂單入住了哪些天數
                    context.dispatch('enumerateDaysBetweenDates',{startDate:data.startDate , endDate:data.endDate})
                    //找出index位置，踢出陣列
                    const dates = res.data[0].date;
                    context.state.DaysBetweenDates.forEach(item=>{
                        const index = dates.indexOf(item);
                        dates.splice(index,1);
                    })
                    //更新新的date回API
                    axios.patch(`${context.state.proxyTableTarget}/disableDate/${data.product_id}`,{
                        date:dates
                    }).then((res)=>{
                        console.log(res,'DelDisableDate : PATCH 請求成功')
                    },(err)=>{
                        console.log(res,'DelDisableDate : PATCH 請求失敗')
                    })
                }
            },(err)=>{
                console.log(err,'DelDisableDate ： GET 請求失敗');
            });
        },
        //計算選取日期期間有幾天為Disable Date
        ThisDisableDayisBetween(context,{start , end}){
            let days = 0;
            context.state.disableDate.forEach(element => {
                //在區間內回傳true
                const answer = moment(element).isBetween(start , end)
                if(answer) days++;
            });
            context.commit('DISABLEDAYSNUM',days);
        },
        //計算所有區間天數
        enumerateDaysBetweenDates(context,{startDate, endDate}) {
            let dates = [];
            //入住那天
            let Previous = moment(startDate).add(-1, 'days')
            //入住前一天
            let PreviousDate = moment(Previous).startOf('day');
            //退房那天
            let lastDate = moment(endDate).startOf('day');
        
            while(PreviousDate.add(1, 'days').diff(lastDate) < 0) {
                dates.push(PreviousDate.format('YYYY/MM/DD'));
            }
            context.commit('DAYSBETWEENDATES',dates);
        }
    },
    mutations:{
        ORDERS(state,payload){
            state.orders = payload;
        },
        SETFINALPRICE(state,{ index , newTotal}){
            state.orders[index].Finalprice = newTotal;
        },
        DISABLEDATE(state,payload){
            state.disableDate = payload;
        },
        DISABLEDAYSNUM(state,payload){
            state.disabledaysNum = payload;
        },
        DAYSBETWEENDATES(state,payload){
            state.DaysBetweenDates = payload;
        }
    },
    getters:{
        orders: state => state.orders,
        disableDate: state => state.disableDate,
        DaysBetweenDates: state => state.DaysBetweenDates,
        disabledaysNum:state => state.disabledaysNum
    }
}


