import axios from 'axios'
//Moment Js 處理時間
import moment from 'moment';

export default{
    strict:true,
    namespaced:true,
    state:{
        double_dorm:{},
        singal_dorm:{},
        mix_dorm:{},
        tent_dorm:{},

        double_dorm_title:[],
        singal_dorm_title:[],
        mix_dorm_title:[],
        tent_dorm_title:[],

        double_dorm_option:"",
        singal_dorm_option:"",
        mix_dorm_option:"",
        tent_dorm_option:"",

        double_dorm_function:"eachIncome",
        singal_dorm_function:"eachIncome",
        mix_dorm_function:"eachIncome",
        tent_dorm_function:"eachIncome",

        allMonthDayNum:[31,29,31,30,31,30,31,31,30,31,30,31],

        proxyTableTarget:'https://limitless-meadow-70828.herokuapp.com'
    },
    actions:{
        getChartData(context){
            axios.get(`${context.state.proxyTableTarget}/chartData`).then((res)=>{
                console.log(res,"getChartData : GET 請求成功");
                context.commit('CHARTDATA',res.data)
                context.dispatch('allTheDormTitle',['double_dorm','singal_dorm','mix_dorm','tent_dorm'])
            },(err)=>{
                console.log(err,"getChartData : GET 請求失敗");
            })
        },
        allTheDormTitle(context,type){
            type.forEach(item=>{
                Object.keys(context.state[item]['each_of_dorm']).forEach((title,index,array)=>{
                    context.commit(`PUSHALLTHETITLEINARRAY`,{type:item,title})
                })
            })
            context.commit('UPDATEDOUBLEOPTION',context.state.double_dorm_title[0]);
            context.commit('UPDATESINGALOPTION',context.state.singal_dorm_title[0]);
            context.commit('UPDATEMIXOPTION',context.state.mix_dorm_title[0]);
            context.commit('UPDATETENTOPTION',context.state.tent_dorm_title[0]);
        }
    },
    mutations:{
        CHARTDATA(state,data){
            state.double_dorm = data[0]["經典雙人房"];
            state.singal_dorm = data[1]["悠閒單人房"];
            state.mix_dorm = data[2]["歡樂混合房"];
            state.tent_dorm = data[3]["野生搭帳"];
        },
        PUSHALLTHETITLEINARRAY(state,{type,title}){
            state[`${type}_title`].push(title);
        },

        UPDATEDOUBLEOPTION(state,payload){
            state.double_dorm_option = payload;
        },
        UPDATESINGALOPTION(state,payload){
            state.singal_dorm_option = payload;
        },
        UPDATEMIXOPTION(state,payload){
            state.mix_dorm_option = payload;
        },
        UPDATETENTOPTION(state,payload){
            state.tent_dorm_option = payload;
        },

        UPDATEDOUBLEFUNCTION(state,payload){
            state.double_dorm_function = payload;
        },
        UPDATESINGALFUNCTION(state,payload){
            state.singal_dorm_function = payload;
        },
        UPDATEMIXFUNCTION(state,payload){
            state.mix_dorm_function = payload;
        },
        UPDATETENTFUNCTION(state,payload){
            state.tent_dorm_function = payload;
        },
    },
    getters:{
        double_dorm: state => state.double_dorm,
        singal_dorm: state => state.singal_dorm,
        mix_dorm: state => state.mix_dorm,
        tent_dorm: state => state.tent_dorm,

        double_dorm_title: state => state.double_dorm_title,
        singal_dorm_title: state => state.singal_dorm_title,
        mix_dorm_title: state => state.mix_dorm_title,
        tent_dorm_title: state => state.tent_dorm_title,

        double_dorm_option: state => state.double_dorm_option,
        singal_dorm_option: state => state.singal_dorm_option,
        mix_dorm_option: state => state.mix_dorm_option,
        tent_dorm_option: state => state.tent_dorm_option,

        double_dorm_function: state => state.double_dorm_function,
        singal_dorm_function: state => state.singal_dorm_function,
        mix_dorm_function: state => state.mix_dorm_function,
        tent_dorm_function: state => state.tent_dorm_function,

        allMonthDayNum: state => state.allMonthDayNum
    }
}


