<template>
    <nav class="d-flex flex-column bg-secondary p-3">
        <router-link class="navbar-brand" to='/index'><img class="Dashboard-navbar" src="@/assets/logo-all-light.svg" alt=""></router-link>
        <h6 class="sidebar-heading d-flex justify-content-between align-items-center px-1 mt-4 mb-1 text-muted font-weight-bold">
        <span class="text-info">管理員</span>
        <a class="d-flex align-items-center text-muted" href="#">
            <span data-feather="plus-circle"></span>
        </a>
        </h6>
        <ul class="nav d-flex flex-column mb-2">
            <li class="nav-item p-1" @click="Sidebarshow">
                <router-link class="d-flex justify-content-around d-md-block nav-link" to="/admin/Dashboard/products">
                <i class="fas fa-box-open text-white"></i>
                <span class="text-white">產品列表</span>
                </router-link>
            </li>
            <li class="nav-item p-1" @click="Sidebarshow">
                <router-link class="d-flex justify-content-around d-md-block nav-link" to="/admin/Dashboard/orderlist">
                <i class="far fa-list-alt text-white"></i>
                <span class="text-white">訂單列表</span>
                </router-link>
            </li>
            <li class="nav-item p-1" @click="Sidebarshow">
                <router-link class="d-flex justify-content-around d-md-block nav-link" to="/admin/Dashboard/analysis">
                <i class="fas fa-chart-line text-white"></i>
                <span class="text-white">數據分析</span>
                </router-link>
            </li>
        </ul>
    </nav>
</template>

<script>
//JQ
import $ from'jquery';
export default {
    data() {
        return {
            
        }
    },
    methods:{
        Sidebarshow(){
            $('.Sidebar')
            .toggleClass('sildeRightOut')
        }
  }
}
</script>
<style lang="scss" scoped>
@import '@/assets/scss/_breakpoint.scss';
.nav-item{
    &:hover{
        transform: translateX(5px);
        transition: .5s cubic-bezier(0.165, 0.84, 0.44, 1);
    }
}
</style>


